.black-nav {
  background : #333;
  width : 100%;
  display : flex;
  color : white;
  padding : 20px;
  }
  
   
  
  div {
  box-sizing : border-box
  }

  
  .list {
  text-align : left;
  padding-left : 20px;
  border-bottom : 1px solid gray;
  }


  .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
    width: 80%;
    height: 50%;
    background-color: #eee;
    padding: 30px;
    
  }